<template>
  <div id="careful-wish-app">
    <div id="query">
      <form action="">
        <input v-model="query"
          type="text"
          name=""
          id=""
          ref="wish"
          placeholder="wish for something..."
          maxlength="25"
          :disabled="searching"
          autofocus>
        <input type="submit" value="Wish" :disabled="searching" @click.capture.prevent="wish">
      </form>
    </div>

    <div v-if="searching" id="searching">
      Searching...
    </div>

    <div v-if="lastWish.length > 0" id="result">
      <div v-if="gotError" class="wish">
        <span class="error">Something bad happened. If this error persists, please contact the author of this puzzle.</span></div>
      <div v-else>
        <div v-if="lastWishNotFound" class="wish">
          <span class="wish-query">{{lastWish}}</span> not found!</div>
        <div v-else>
          <div class="wish">Here is your <span class="wish-query">{{lastWish}}</span>:</div>
          <div class="img"><img :src="img"></div>
        </div>
      </div>
    </div>

  </div>
</template>

<script>

export default {
  name: 'CarefulWishApp',
  data() {
    return {
      query: '',
      lastWish: '',
      img: '',
      searching: false,
      lastWishNotFound: false,
      gotError: false,
    }
  },

  computed: {
    queryClean: function () {
      return this.query.toLowerCase().replace(/[^a-z ]/gi, '').trim()
    },
  },

  methods: {

    reset: function () {
      this.lastWish = this.query
      this.query = ''
      this.searching = false
      this.$refs.wish.disabled = false
      this.$refs.wish.focus()
    },

    makeWish: async function (query) {
      let queryEncoded = encodeURI(query)
      let url = `https://careful-wish.herokuapp.com/api/wish?wish=${queryEncoded}`
      let options = {
        method: 'GET',
        headers: {
          'Accept': '*/*',
        }
      }
    
      return fetch(url, options)
        .then(response => {
          return response.json()
        })
        .catch(err => {
          this.gotError = true
          return false
        })
    
    },

    wish: async function () { 

      this.searching = true

      if (this.queryClean === "") {
        this.reset()
        this.lastWish = ''
        this.gotError = false
        return
      }

      let img = await this.makeWish(this.queryClean)

      if (img === "not found") {
        this.lastWishNotFound = true
      } else if (img) {
        this.img = img
        this.lastWishNotFound = false
        this.gotError = false
      } else {
        this.lastWishNotFound = true
      }
      this.reset()

    },
  },

}
</script>

